@import url(https://fonts.googleapis.com/css?family=Satisfy&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
* {
  padding: 0;
  margin: 0;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

body {
  background-color: #222831;
  color: #fff;
}
.contact {

  margin-top: 80px;
}

.contact h1 {
  font-family: 'Satisfy', cursive;
  font-size: 62px;
  text-align: center;
}

.form {
  margin-left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  width: 50%;
  
}

.form input,
textarea {
  width: 100%;
  outline: none;
  background-color: #393E46;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 20px;
}
.formBtn{
  outline: none;
  background-color: #393E46;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  
}
.contact form label {
  display: block;
}

@media only screen and (max-width: 600px) {
  .form{
    width: 90%;
  }
}
.about {
  text-align: center;
  margin-top: 80px;
  padding-bottom: 100px;
}

.about h1 {
  font-family: "Satisfy", cursive;
  font-size: 62px;
}

.about p {
  text-align: left;
  width: 60%;
  margin: 0 auto;
  line-height: 25px;
  font-size: 20px;
  opacity: 0.9;
}
.about h3 {
  text-align: left;
  width: 60%;
  margin: 15px auto 3px auto;
}
.socialIcons {
  margin: 20px;
  font-size: 22px;
}
.socialIcons a {
  margin: 5px;
  padding: 10px;
}
@media only screen and (max-width: 600px) {
  .about p {
    width: 90%;
  }
}

.works {
  text-align: center;
  margin-bottom: 100px;
}

hr {
  width: 30%;
  margin: 30px auto;
  opacity: .3;
}

.works h1 {
  font-family: 'Satisfy', cursive;
  font-size: 62px;
  opacity: .8;
}

.work-item {
  width: 60%;
  margin: 50px auto;
}

.work-item h3 {
  font-size: 32px;
}

.work-item p {
  margin-bottom: 20px;
}

.work-item code {
  background-color: #393E46;
  padding: 0 10px;
}

.work-item img {
  width: 100%;
  margin-bottom: 5px;
  border-radius: 5px;
}

.work-item a {
  padding: 10px;
  color: #fff;
  opacity: .8;
}

.work-item a:hover {
  opacity: 1;
}

@media only screen and (max-width: 600px) {
  .work-item {
    width: 90%;
  }

  .work-item h3 {
    margin: 30px 0;
  }
}
.home {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 600px;
  height: 100vh;
}

.home small {
  display: block;
}

.home h1 {
  font-family: 'Satisfy', cursive;
  font-size: 62px;
}

.home p {
  opacity: .5;
}

.particles {
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100vw;
  height: 100vh;
  -webkit-filter: brightness(50%);
          filter: brightness(50%);
}

.home button {
  padding: 10px;
  width: 100px;
  margin: 20px 5px;
  border: 1px solid #fff;
  background-color: transparent;
  color: #fff;
  cursor: pointer;
  opacity: .9;
}

.home button:hover {
  opacity: 1;
}

@media only screen and (max-width: 600px) {
  .home div {
    margin-top: -100px;
  }
}
.navLists {
  display: flex;
  justify-content: space-evenly;

  position: fixed;
  z-index: 10;
  bottom: 0;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  padding: 10px;
  background-color: #222831;
  width: 50%;
}

a {
  color: #fff;
}

.navLists li a i {
  opacity: .8;
  font-size: 22px;
  padding: 10px;
}

.navLists li a i:hover {
  opacity: 1;
}

@media only screen and (max-width: 600px) {
  .navLists {
    width: 100%;
  }
}
